/*Quote.css*/
.quote {
	padding: 4rem;
	background-color: #fff;
	max-width: 50%;
	margin: auto;
	border-radius: 3px;
}
.quote-text {
	font-size: 2.6rem;
	font-family: 'Josefin Sans', 'Arial', sans-serif;
	padding-bottom: 2rem;
}
.quote-author {
	font-size: 1.2rem;
	font-family: 'Roboto Slab', serif;
	padding-bottom: 2rem;
}
.quote-src {
	font-size: 1.2rem;
	font-family: 'Roboto Slab', serif;
}
/*TweetBtn.css*/
body {
    margin: 0px;
	background-color: #989CA5;
}
.quote-box {
	text-align: center;
	margin-top: 80px;
}
.btn-panel {
	display: flex;
	justify-content: space-evenly;
	padding-top: 40px;
}
.btn {
	padding: 12px 20px;
    background-color: #26BF93;
    min-width: 80px;
    border: none;
    border-radius: 2px;
	font-family: 'Roboto Slab', serif;
	color: #fff;
	font-size: 1.2rem;
}
a.btn {
	text-decoration: none;
}
.btn:hover {
	cursor: pointer;
	background-color: #9CD8C7;
}
